import store from './store';

const MS_IN_SEC = 1000;
const SEC_IN_MIN = 60;
const MIN_IN_HOUR = 60;
const MS_IN_MIN = MS_IN_SEC * SEC_IN_MIN;
const MS_IN_HOUR = MS_IN_MIN * MIN_IN_HOUR;
const SEC_IN_HOUR = SEC_IN_MIN * MIN_IN_HOUR;

function zfill(src, count) {
  if (!store.settings.padWithZero) {
    return src;
  }

  let result = '';
  for (let i = 0; i < count - String(src).length; i++) {
    result += '0';
  }
  result += String(src);

  return result;
}

export default {
  convertFromObject(obj) {
    return obj.m * MS_IN_MIN + obj.s * MS_IN_SEC + obj.ms;
  },
  zero() {
    return 0;
  },
  add(a, b) {
    if (!a) {
      a = this.zero();
    }
    if (!b) {
      b = this.zero();
    }

    return a + b;
  },
  sub(a, b) {
    if (!a) {
      a = this.zero();
    }
    if (!b) {
      b = this.zero();
    }

    return a - b;
  },
  sort(a, b) {
    const gt = this.gt(a, b);
    if (gt) {
      return 1;
    }
    return -1;
  },
  gt(a, b) {
    return a > b;
  },
  string(a) {
    if (!a || a < 0) {
      a = this.zero();
    }
    let milliseconds = a;
    const hours = Math.floor(milliseconds / MS_IN_HOUR);
    milliseconds -= hours * MS_IN_HOUR;
    const minutes = Math.floor(milliseconds / MS_IN_MIN);
    milliseconds -= minutes * MS_IN_MIN;
    const seconds = Math.floor(milliseconds / MS_IN_SEC);
    milliseconds -= seconds * MS_IN_SEC;

    if (store.settings.showHours) {
      return `${zfill(hours, 2)}:${zfill(minutes, 2)}:${zfill(seconds, 2)}:${zfill(milliseconds, 3)}`;
    }
    return `${zfill(minutes, 2)}:${zfill(seconds, 2)}:${zfill(milliseconds, 3)}`;
  },
  cpstring(a) {
    if (!a) {
      return '0';
    }
    return String(a / 1000);
  },
  parse(str) {
    if (!str.split) {
      return this.zero();
    }
    let parts = str.split(':');
    if (str.indexOf(':') < 0) {
      parts = str.split('.');
    }

    switch (parts.length) {
      case 0:
        return this.zero();

      case 1:
        return Number(str) * MS_IN_SEC;

      case 2:
        return Number(parts[0]) * MS_IN_MIN +
          Number(parts[1]) * MS_IN_SEC;

      case 3:
        return Number(parts[0]) * MS_IN_MIN +
          Number(parts[1]) * MS_IN_SEC +
          Number(parts[2]);

      case 4:
        Number(parts[0]) * MS_IN_HOUR +
          Number(parts[1]) * MS_IN_MIN +
          Number(parts[2]) * MS_IN_SEC +
          Number(parts[3])
    }
  },
  isTime(str) {
    if (!str) {
      return false;
    }
    if (!str.split) {
      return false;
    }
    const parts = str.split(':');
    if (
      parts.length === 1 ||
      parts.length >= 3
    ) {
      return true;
    }

    return false;
  },
  min(a, b) {
    if (this.gt(a, b)) {
      return b;
    }
    return a;
  },
};


