function isPrintVersion() {
  const isPrint = window.location.search.indexOf('print') >= 0;
  return isPrint;
}

function isStartProtocol() {
  const isPrint = window.location.search.indexOf('startProtocol') >= 0;
  return isPrint;
}

function openInNewTab(pathname) {
  window.open((window.location.pathname + pathname).replace('//', '/'), '_blank');
}

export default {
  isPrintVersion,
  isStartProtocol,
  openInNewTab,
};



